
.modal_wrap{
    width: 100%;
    height: 100%;
    position: fixed;
    left: 0;
    top: 0;
    background: rgba(0, 0, 0, 0.7);
    z-index: 8;
}


.modal{
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    max-width: 920px;
    height: auto;
    z-index: 9;
}

.modal .modal_top{
    position: relative;
}

.modal .modal_top .btn_close{
    cursor: pointer;
    font-size: 30px;
    color: rgba(255, 255, 255, 0.9);
    position: absolute;
    right: 20px;
    top: 20px;
}

.modal .modal_top .gradient{
    width: 100%;
    height: 60px;
    background: linear-gradient(to top, #222222 0%, #22222200 100%);
    position: absolute;
    left: 0;
    bottom: -2px;
}

.modal img{
    width: 100%;
}

.modal .modal_top h2{
    position: absolute;
    left: 20px;
    top: 0;
    font-size: 60px;
    text-shadow: #0000009c 4px 4px 8px;
}

.modal .modal_bot{
    gap: 20px;
    background: #222222;
    padding: 20px;
}

.modal .modal_bot .info_left{
    width: 100%;
}

.modal .modal_bot .info_left h3{
    margin-bottom: 20px;
}

.modal .modal_bot .info_left p{
    font-size: 14px;
    color: #c5c5c5;
    font-weight: bold;
}

.modal .modal_bot .info_left p span{
    color: #61ed78;
    margin-right: 10px;
    font-size: 14px;
    font-weight: bold;
}

.modal .modal_bot .info_left .overview{
    color: #fff;
    font-size: 15px;
    font-weight: normal;
    line-height: 28px;
    margin-top: 20px;
}