/* header */

.header{
    width: 100%;
    height: 70px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: fixed;
    left: 0; top: 0;
    padding: 0 60px;
    background: linear-gradient(to top, #14141400 0%, #14141498 100%);
    z-index: 2;
    transition: all 0.5s ease;
}

.header li{
    cursor: pointer;
}

.header .hgnb_wrap{
    display: flex;
    gap: 45px;
}

.header .hgnb_wrap .gnb{
    display: flex;
    gap: 22px;
    align-items: center;
}

.header .logo{
    width: 90px;
    cursor: pointer;
}

.header .avartar{
    width: 32px;
}

.header .icon_wrap{
    display: flex;
    gap: 22px;
    align-items: center;
}

.header_black{
    background-color: #141414;
}