/* all movie */

.allmovie{
    width: 100%;
}

.allmovie h2{
    padding-left: 60px;
    margin-top: 60px;
    margin-bottom: 16px;
    color: #ededed;
}

.allmovie .posters{
    display: flex;
    padding-left: 60px;
}

.allmovie .posters .allmovie_img{
    display: block;
    width: 100%;
    max-width: 200px;
    object-fit: contain;
    margin-right: 10px;
    border-radius: 4px;
    transition: all 0.3s ease-in-out;
}

.allmovie .posters .allmovie_img:hover{
    transform: scale(1.05);
}