/* banner */

.banner{
    width: 100%;
    height: 100vh;
    object-fit: contain;
    background-position: center top;
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;
}

.banner .content{
    position: absolute;
    left: 70px;
    bottom: 180px;
}

.banner .content .title h1{
    font-size: 80px;
    font-weight: bold;
    padding-bottom: 50px;
    text-shadow: #000 4px 4px 12px;
}

.banner .content .btn_wrap{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 14px;
    padding-bottom: 50px;
}

.banner .content .btn_wrap button{
    height: 56px;
    font-size: 22px;
    padding: 0 35px;
    border-radius: 4px;
    transition: all 0.5s;
}

.banner .content .btn_wrap button .svg-inline--fa{
    margin-right: 8px;
}

.banner .content .btn_wrap button:first-child{
    background: #fff;
    color: #000;
}

.banner .content .btn_wrap button:first-child:hover{
    background: #636365;
    color: #fff;
}

.banner .content .btn_wrap button:last-child{
    background: #636365;
    color: #fff;
}

.banner .content .btn_wrap button:last-child:hover{
    background: #fff;
    color: #000;
}

.banner .content .discription{
    width: 720px;
    line-height: 1.5;
    font-weight: bold;
    font-size: 20px;
}